import { useEffect, useState } from "react";
import styled from "styled-components";
import { Button, CircularProgress, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";
import Countdown from "react-countdown";
import { CTAButton } from "../components/CTAButton";
import { useAnchorWallet } from "@solana/wallet-adapter-react";
import { awaitTransactionSignatureConfirmation, getCandyMachineState, mintOneToken } from "./candy-machine";
import Twitter from "../assets/tw.svg";
import Discord from "../assets/discord.svg";
import { MintingCountdown } from "../components/MintingCountdown";
import { Title } from "../components/Title";
import { YellowTitle } from "../components/YellowTitle";
import { useConfetti } from "../components/Confetti";
import { MintList } from "../components/MintList";

const preSaleStart = new Date("2021-10-21T18:40:00+00:00");
const saleStart = new Date("2021-10-21T19:00:00+00:00");

const isLaunched = function (launchDate) {
  const now = new Date();
  console.log(now.setHours(0, 0, 0, 0));
  console.log(launchDate.setHours(0, 0, 0, 0));
  if (now.setHours(0, 0, 0, 0) >= launchDate.setHours(0, 0, 0, 0)) {
    return true;
  }

  return false;
};

export const Minting = (props) => {
  const [isActive, setIsActive] = useState(false); // true when countdown completes
  const [isSoldOut, setIsSoldOut] = useState(false); // true when items remaining is zero
  const [isMinting, setIsMinting] = useState(false); // true when user got to press MINT

  // console.log(isLaunched(props.preSale ? preSaleStart : saleStart));
  // console.log("ar jau pased", isLaunched(props.preSale ? preSaleStart : saleStart));

  const { dropConfetti } = useConfetti();

  const [itemsRedeemed, setItemsRedeemed] = useState(0);

  const [alertState, setAlertState] = useState({
    open: false,
    message: "",
    severity: undefined,
  });

  const [startDate, setStartDate] = useState(new Date(props.startDate));

  const wallet = useAnchorWallet();
  const [candyMachine, setCandyMachine] = useState();

  // console.log(props.preSale);

  const refreshCandyMachineState = () => {
    (async () => {
      if (!wallet) return;

      const { candyMachine, goLiveDate, itemsRemaining, itemsRedeemed } = await getCandyMachineState(
        wallet,
        props.candyMachineId,
        props.connection
      );

      setItemsRedeemed(itemsRedeemed);

      setIsSoldOut(itemsRemaining === 0);
      setStartDate(goLiveDate);
      setCandyMachine(candyMachine);
    })();
  };

  const onMint = async () => {
    try {
      setIsMinting(true);
      if (wallet && candyMachine?.program) {
        const mintTxId = await mintOneToken(candyMachine, props.config, wallet.publicKey, props.treasury);

        const status = await awaitTransactionSignatureConfirmation(
          mintTxId,
          props.txTimeout,
          props.connection,
          "singleGossip",
          false
        );

        if (!status?.err) {
          setAlertState({
            open: true,
            message: "Congratulations! Mint succeeded!",
            severity: "success",
          });
          dropConfetti();
        } else {
          setAlertState({
            open: true,
            message: "Mint failed! Please try again!",
            severity: "error",
          });
        }
      }
    } catch (error) {
      // TODO: blech:
      let message = error.msg || "Minting failed! Please try again!";
      if (!error.msg) {
        if (error.message.indexOf("0x138")) {
        } else if (error.message.indexOf("0x137")) {
          message = `SOLD OUT!`;
        } else if (error.message.indexOf("0x135")) {
          message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
          message = `SOLD OUT!`;
          setIsSoldOut(true);
        } else if (error.code === 312) {
          message = `Minting period hasn't started yet.`;
        }
      }

      setAlertState({
        open: true,
        message,
        severity: "error",
      });
    } finally {
      setIsMinting(false);
      refreshCandyMachineState();
    }
  };

  useEffect(refreshCandyMachineState, [wallet, props.candyMachineId, props.connection]);
  return (
    <div className="row">
      <div className="col-12 d-flex justify-content-center mb-5">
        {isActive ? (
          !isSoldOut && (
            <Title bold large>
              Minting has started!
            </Title>
          )
        ) : (
          <div>
            <Title bold>Minting starts in</Title>

            <Countdown
              date={props.preSale ? preSaleStart : saleStart}
              renderer={MintingCountdown}
              onMount={({ completed }) => completed && setIsActive(true)}
              onComplete={() => setIsActive(true)}
            />
          </div>
        )}
      </div>

      {wallet && itemsRedeemed > 0 && (
        <div className="col-12 d-flex justify-content-center" style={{ marginBottom: 45 }}>
          <Title>
            Congratulations! <br /> You've minted {itemsRedeemed} ticket{itemsRedeemed > 1 ? "s" : ""}/tameggo
            {itemsRedeemed > 1 ? "s" : ""}.
          </Title>
        </div>
      )}

      {wallet && !isSoldOut && (
        <div className="col-12 d-flex justify-content-center mb-5">
          <CTAButton disabled={isSoldOut || isMinting || !isActive} onClick={onMint} variant="contained">
            {isMinting ? <CircularProgress /> : itemsRedeemed > 0 ? "Mint again" : "MINT"}
          </CTAButton>
        </div>
      )}

      {/* {!isSoldOut && (
        <div className="col-12">
          <MintList />
        </div>
      )} */}

      {isSoldOut && (
        <>
          <div className="col-12">
            <Title bold large>
              Sold out!
            </Title>
          </div>
          <div className="col-12 mb-4">
            <YellowTitle className="mb-4">
              The game will commence within 48 hours. <br /> Happy hatching!
            </YellowTitle>
            <div className="d-flex justify-content-center">
              <a
                className="d-flex align-items-center me-4"
                href="https://twitter.com/JikanStudios"
                target="_blank"
                rel="noopener noreferrer"
                style={{ textDecoration: "none" }}
              >
                <img src={Twitter} className="me-2" />
                <Title className="mb-0" small>
                  @twitter
                </Title>
              </a>
              <a
                className="d-flex align-items-center"
                href="https://discord.com/invite/BTZzAhR9MD"
                target="_blank"
                rel="noopener noreferrer"
                style={{ textDecoration: "none" }}
              >
                <img src={Discord} className="me-2" />
                <Title className="mb-0" small>
                  @discord
                </Title>
              </a>
            </div>
          </div>
        </>
      )}

      {!isSoldOut && wallet && (
        <div className="col-12">
          <MintList />
        </div>
      )}

      <Snackbar
        open={alertState.open}
        autoHideDuration={6000}
        onClose={() => setAlertState({ ...alertState, open: false })}
      >
        <Alert onClose={() => setAlertState({ ...alertState, open: false })} severity={alertState.severity}>
          {alertState.message}
        </Alert>
      </Snackbar>
    </div>
  );
};
