import styled from "styled-components";

export const styledPropsMatcher = (modifiers) => {
  return (params) => {
    const propsKeys = Object.keys(modifiers);
    const matchingModifier = Object.keys(params)
      .filter((key) => typeof params[key] === "boolean" && params[key])
      .filter((param) => propsKeys.includes(param))
      .pop();

    if (!matchingModifier) {
      return false;
    }
    return modifiers[matchingModifier];
  };
};

const getSize = styledPropsMatcher({
  small: 14,
  large: 39,
});

export const Title = styled.h3`
  font-weight: ${({ bold }) => bold && 500};
  font-size: ${(params) => getSize(params) || 24}px;
  text-align: center;
  letter-spacing: -0.003em;
  color: #f7f5fb;
  line-height: 150%;
`;
