import styled from "styled-components";
import { useWallet } from "@solana/wallet-adapter-react";
import { WalletMultiButton } from "@solana/wallet-adapter-react-ui";

const Container = styled.div`
  position: absolute;
  top: 32px;
  right: 45px;
`;

const Title = styled.div`
  font-size: 14px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #f7f5fb;
  margin-right: 15px;
`;

const GreenDot = styled.div`
  width: 10px;
  height: 10px;
  background: #00a6a6;
  border-radius: 50%;
`;

export const Connected = (props) => {
  const { connected } = useWallet();
  // useEffect(() => console.log(connection), [connection]);
  if (!connected) return null;
  return (
    <Container className="d-flex align-items-center justify-content-center" {...props}>
      <Title>Wallet connected</Title>
      <WalletMultiButton style={{ backgroundColor: "transparent" }} endIcon={<GreenDot />} />
    </Container>
  );
};
