import styled from "styled-components";
import StaminaLightning from "../../../assets/items/Stamina.svg";
import { Help } from "../../Help";

const Button = styled.div`
  /* padding: 16px 77px; */

  background: ${({ sell }) => (sell ? "#210124" : "#be1e2d")};
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  appearance: none;
  border: none;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  text-transform: uppercase;
  ${({ sell }) => !sell && `border: 1px solid #700711;`}
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  width: 76px;
  height: 40px;

  &:active {
    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.15);
  }

  &:hover {
    ${({ interact }) => interact && `width: 103px;`}

    /* transition: all ease-in-out 100ms; */
    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.15);

    span.use-text {
      display: block;
    }
  }

  img {
    height: 22px;
    margin-right: 5px !important;
  }

  span {
    display: none;
    font-weight: 500;
    font-size: 14px;
    margin-right: 5px;
    line-height: 21px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #f7f5fb;
  }
`;

export const MenuItemContainer = styled.li`
  background: #ffbd59;
  padding: 42px 32px;
  position: relative;
  display: flex;
  justify-content: space-between;

  &:nth-child(2n) {
    background: #ffc976;
  }

  .use-btns {
    opacity: 0;
  }

  &:hover {
    background: rgb(190, 30, 45);
    background: linear-gradient(90deg, rgba(190, 30, 45, 1) 1.5%, rgba(255, 189, 89, 1) 1.5%);

    &:nth-child(2n) {
      background: rgb(190, 30, 45);
      background: linear-gradient(90deg, rgba(190, 30, 45, 1) 1.5%, rgba(255, 201, 118, 1) 1.5%);
    }

    .use-btns {
      opacity: 1;
    }

    ${Button} {
      ${({ isInteract }) => isInteract && `width: 103px;`}

      span.use-text {
        display: block;
      }
    }
  }

  img {
    margin-right: 15px;
  }
`;

const ItemDescription = styled.span`
  font-size: 14px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #210124;
  margin-right: 10px;
`;

export const MenuList = styled.ul`
  border-radius: 10px;
  /* overflow: scroll; */
  width: 445px;
  border-radius: 10px;
  list-style: none;
  padding: 0;
  max-height: 700px;
  margin: 0 15px 0 0;

  overflow-y: auto;

  &::-webkit-scrollbar {
    /* width: 5px; */
  }
`;

export const MenuItem = ({ icon, title, cost, onInteractUse, onItemUse, itemName, amount = undefined, explain }) => {
  const handleClick = () => onInteractUse({ cost, itemName });
  const handleItemUse = (action) => onItemUse({ action, itemName });
  const isInteract = amount === undefined && onInteractUse;
  const isInventory = amount !== undefined && onItemUse;
  return (
    <MenuItemContainer isInteract={isInteract}>
      <div className="d-flex  position-relative">
        <img src={icon} alt="" style={{ width: 50, marginLeft: 10 }} />
        <div className="d-flex flex-column justify-content-center">
          <div className="d-flex">
            <ItemDescription>{title}</ItemDescription>
            <Help
              red
              text={explain}
              style={{ position: "absolute", left: -16, top: "50%", transform: "translateY(-50%)" }}
            />
          </div>
          {amount !== undefined && <span>x {amount}</span>}
        </div>
      </div>
      {isInteract && (
        <div>
          <Button interact onClick={handleClick}>
            <span className="use-text">use</span>
            <img src={StaminaLightning} alt="" />
            {cost}
          </Button>
        </div>
      )}
      {isInventory && (
        <div className="d-flex align-items-center use-btns">
          <div className="d-flex flex-column" className="me-2">
            <Button onClick={handleItemUse} className="mb-1" onClick={() => handleItemUse("USE_ONE")}>
              Use 1
            </Button>
            <Button onClick={() => handleItemUse("USE_ALL")}>Use all</Button>
          </div>
          <Button sell onClick={() => handleItemUse("SELL")}>
            Sell
          </Button>
        </div>
      )}
    </MenuItemContainer>
  );
};
