import { resolveIconByName } from "../utils/resolveIconByName";

const InteractionAnimationMainKeyframes = keyframes`
    0% { 
      opacity: 0;
     }
    30% {
      opacity: 1;
      height: 80px;
      width: 80px;
    }
    40%{
      height: 60px;
      width: 60px;
    } 
    50% {
      height: 80px;
      width: 80px;
      transform: translateY(0px); 
    }
    100% {
       transform: translateY(-400px); 
    }
`;

const InteractionAnimation1Keyframes = keyframes`
    0% { 
      opacity: 0;
     }
    60% {
      opacity: 1;
      transform: translateY(0px); 
    }
    100% {
      opacity: 0;
      transform: translateY(-300px); 
    }
`;

