const EggGlowKeyFrames = keyframes`
     0% { opacity: 0; }
    /* 50% { opacity: 0.5; } */
    70% { opacity: 0.9; }
    100% { opacity: 0; }
`;

const EggGlow = styled.div`
  position: absolute;
  width: 200px;
  height: 200px;
  bottom: 0;
  top: 0;
  left: 0;
  opacity: 0;
  right: 0;
  margin: auto;
  background: #f7f5fb;
  filter: blur(50px);
  z-index: 21341234;
  border-radius: 50%;
  animation: ${EggGlowKeyFrames} 1.5s ease-in-out 1;
`;

const Meteor = styled.div`
  width: 40px;
  height: 200px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  z-index: 32432423;

  top: 0;
  right: 0;
  left: 0;
  margin: auto;
  filter: blur(18px);

  &:before {
    position: absolute;
    content: "";
    width: 50px;
    height: 50px;
    bottom: 0;
    background-color: white;
    z-index: 100000000;
  }
`;

const MeteorKeyFrames = keyframes`
    0% { 
      opacity: 0;
      transform: translateY(-500px); 
     }
    10% { 
      opacity: 1;
      transform: translateY(-500px); 
     }
     90% {
      opacity: 1;
      transform: translateY(100px); 
    }
    100% {
      opacity: 0;
      transform: translateY(100px); 
    }
`;

const MeteorContainer = styled.div`
  width: 100px;
  height: 200px;
  position: absolute;
  right: 0;
  transform: translateY(-500px);
  left: 0;
  margin: auto;
  z-index: 120;
  opacity: 0;
  animation: ${MeteorKeyFrames} 1.5s ease-in-out 1;

  img {
    position: absolute;
    right: 0;
    left: 0;
    margin: auto;
    bottom: 0;
    z-index: 100000001;
  }
`;

export const InteractionAnimation = ({ icon, onComplete, isInteraction, isItem }) => {
  const [interactionAnimationSteps, setInteractionAnimationStep] = useState({
    animMain: false,
    anim1: false,
    anim2: false,
    anim3: false,
    anim4: false,
    anim5: false,
    anim6: false,
  });

  useEffect(
    () => Object.values(interactionAnimationSteps).every((val) => val === true) && onComplete(),
    [interactionAnimationSteps]
  );

  const resolvedIcon = useMemo(() => resolveIconByName(icon), [icon]);

  if (isItem) {
    return (
      <div>
        {/* <EggGlow /> */}
        <MeteorContainer onAnimationEnd={() => onComplete()}>
          <Meteor></Meteor>
          <img src={resolvedIcon} alt="" />
        </MeteorContainer>
      </div>
    );
  }

  if (isInteraction)
    return (
      <div>
        <EggGlow />
        <InteractionAnimationMain
          onAnimationEnd={() => {
            setInteractionAnimationStep({ ...interactionAnimationSteps, animMain: true });
          }}
          src={resolvedIcon}
        />
        <InteractionAnimation1
          onAnimationEnd={() => {
            setInteractionAnimationStep({ ...interactionAnimationSteps, anim1: true });
          }}
          src={resolvedIcon}
        />
        <InteractionAnimation2
          onAnimationEnd={() => {
            setInteractionAnimationStep({ ...interactionAnimationSteps, anim2: true });
          }}
          src={resolvedIcon}
        />
        <InteractionAnimation3
          onAnimationEnd={() => {
            setInteractionAnimationStep({ ...interactionAnimationSteps, anim3: true });
          }}
          src={resolvedIcon}
        />
        <InteractionAnimation4
          onAnimationEnd={() => {
            setInteractionAnimationStep({ ...interactionAnimationSteps, anim4: true });
          }}
          src={resolvedIcon}
        />
        <InteractionAnimation5
          onAnimationEnd={() => {
            setInteractionAnimationStep({ ...interactionAnimationSteps, anim5: true });
          }}
          src={resolvedIcon}
        />
        <InteractionAnimation6
          onAnimationEnd={() => {
            setInteractionAnimationStep({ ...interactionAnimationSteps, anim6: true });
          }}
          src={resolvedIcon}
        />
      </div>
    );
};
