import styled from "styled-components";
import { NavLink } from "react-router-dom";
import MenuInteractLogo from "../../../assets/menu_pointer.svg";
import MenuStoreLogo from "../../../assets/menu_store.svg";
import MenuHatchLogo from "../../../assets/menu_hatch.svg";
import MenuInventoryLogo from "../../../assets/menu_inventor.svg";
import MenuMarketLogo from "../../../assets/menu_marketplace.svg";

const PageNavigationButton = styled(NavLink)`
  width: 180px;
  height: 64px;
  background: #be1e2d;
  border: 1px solid #700711;
  display: flex;
  align-items: center;
  border-radius: 5px;
  padding: 0 20px;
  margin-bottom: 15px;
  cursor: pointer;
  text-decoration: none;

  &:active {
    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.15);
  }

  &:hover {
    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.15);
  }

  &.active {
    background: #ffbd59;
    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.15);
    span {
      color: #210124;
    }
  }

  img {
    margin-right: 12px;
  }

  span {
    font-size: 14px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #f7f5fb;
  }
`;

export const NavBar = ({ disabled }) => {
  return (
    <div>
      <PageNavigationButton to="/egg/interact">
        <img src={MenuInteractLogo} alt="" />
        <span>Interact</span>
      </PageNavigationButton>
      <PageNavigationButton
        to={{ pathname: disabled ? "/egg/interact" : "https://google.com" }}
        target="_blank"
        rel="noopener noreferrer"
      >
        <img src={MenuStoreLogo} alt="" />
        <span>Store</span>
      </PageNavigationButton>
      <PageNavigationButton to={disabled ? "/egg/interact" : "/egg/hatch-box"}>
        <img src={MenuHatchLogo} alt="" />
        <span>Hatch box</span>
      </PageNavigationButton>
      <PageNavigationButton to={disabled ? "/egg/interact" : "/egg/inventory"}>
        <img src={MenuInventoryLogo} alt="" />
        <span>Inventory</span>
      </PageNavigationButton>
      <PageNavigationButton
        to={{ pathname: disabled ? "/egg/interact" : "https://google.com" }}
        target="_blank"
        rel="noopener noreferrer"
      >
        <img src={MenuMarketLogo} alt="" />
        <span>Marketplace</span>
      </PageNavigationButton>
    </div>
  );
};
