import styled from "styled-components";
import ReactTooltip from "react-tooltip";

const HelpContainer = styled.div`
  width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: help;

  ${({ red }) =>
    red &&
    `
  background-color: #BE1E2D;
  color: white;
  `}
  ${({ yellow }) =>
    yellow &&
    `
  background-color: #FFBD59;
  color: black;
  `}
`;

export const Help = ({ red, yellow, text, ...rest }) => {
  return (
    <>
      <ReactTooltip id="enrich" />
      <HelpContainer data-for="enrich" data-tip={text} red={red} yellow={yellow} {...rest}>
        ?
      </HelpContainer>
    </>
  );
};
