import { useState } from "react";
import styled from "styled-components";

const Button = styled.button`
  color: white;
  appearance: none;
  border: none;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  text-transform: uppercase;
  border-radius: 5px;
  cursor: pointer;
  text-decoration: none;
  padding: 8px;
  width: 36px;
  height: 36px;
  background: #be1e2d;
  border-radius: 5px;
  margin-right: 5px;

  &:active {
    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.15);
  }

  &.active {
    background: #ffbd59;
    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.15);
    span {
      color: #210124;
    }
  }

  &:hover {
    box-shadow: inset 4px 4px 4px rgba(0, 0, 0, 0.15);
  }
  /* &:disabled {
    opacity: 0.8;
  } */
`;

const EggPagesNavigationContainer = styled.div`
  position: absolute;
  bottom: 38px;
  /* right: 87px; */
  display: flex;
  justify-content: center;
  width: 100%;
`;

export const EggPagesNavigation = ({ disable, onPageChange, eggsFetched, onEggAdd }) => {
  const [crr, setCrr] = useState(0);
  return (
    <EggPagesNavigationContainer>
      {eggsFetched.map((egg, index) => (
        <Button
          key={index}
          disabled={disable}
          className={`${crr === index ? "active" : ""}`}
          onClick={() => {
            setCrr(index);
            onPageChange(index);
          }}
        >
          {index + 1}
        </Button>
      ))}
      <Button onClick={() => onEggAdd()}>+</Button>
    </EggPagesNavigationContainer>
  );
};
