import { useEffect, useState } from "react";
import styled, { keyframes } from "styled-components";
import { Items } from "../utils/itemsEnum";
import { resolveIconByName } from "../utils/resolveIconByName";

const InteractionTitleContainerKeyFrames = keyframes`
    0% { opacity: 0; }
    50% { opacity: 1; }
    100% { opacity:0; }
`;

const InteractionTitleContainer = styled.div`
  opacity: 0;
  animation: ${InteractionTitleContainerKeyFrames} 5s ease-in-out 1;
  display: flex;
  justify-content: center;

  img {
    margin-right: 20px;
    height: 21px;
  }

  div {
    font-size: 14px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: white;
  }
`;

export const EggAnimationTitle = ({ icon }) => {
  const [itemToAnimate, setItemToAnimate] = useState(false);
  useEffect(() => {
    if (itemToAnimate && !!icon) {
      setItemToAnimate(false);
      setTimeout(() => setItemToAnimate(icon), 0);
      return;
    }
    if (!!icon) {
      setItemToAnimate(icon);
      return;
    }
  }, [icon]);
  if (!itemToAnimate) return <div style={{ height: 21 }}></div>;
  return (
    <InteractionTitleContainer isActive={itemToAnimate} onAnimationEnd={() => setItemToAnimate(false)}>
      <img src={resolveIconByName(itemToAnimate)} alt="" />
      <div>{Items[itemToAnimate]?.animationDescription}</div>
    </InteractionTitleContainer>
  );
};
