import { useState } from "react";
import styled, { css } from "styled-components";
import EggStamina from "../assets/egg_stamina1.png";
import Countdown from "react-countdown";

const StaminaContainer = styled.div`
  width: 24px;
  height: 455px;

  border-radius: 20px;
  background: #f7f5fb;
  border: 1px solid #adadad;
  box-sizing: border-box;
  overflow: hidden;
  display: flex;
`;

const Container = styled.div`
  width: 24px;
  height: 455px;
  left: 13px;
  top: 3px;
  z-index: 99;
`;

const EggStaminaContainer = styled.img`
  width: 68px;
  height: 80px;
  position: absolute;
  bottom: -66px;
  left: -22px; ;
`;

const StaminaFlow = styled.div`
  background: linear-gradient(90deg, #a93cd3 -25%, #641fb2 47.17%, #a23eb9 120.83%);
  width: 24px;
  height: ${({ progress }) => (progress / 10) * 45.5}px;
  margin-top: auto;
  transition: all cubic-bezier(0.25, 0.46, 0.45, 0.94) 400ms;
`;

const StaminaCount = styled.div`
  position: absolute;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
  letter-spacing: 0.03em;
  color: ${({ isActive }) => (isActive ? "#FFBD59" : "#f7f5fb")};
  transition: all cubic-bezier(0.25, 0.46, 0.45, 0.94) 400ms;
  left: -31px;

  ${({ noTick }) =>
    !noTick &&
    css`
      &:after {
        content: "";
        position: absolute;
        right: -21px;
        background-color: white;
        height: 1px;
        width: 8px;
        top: 10px;
      }
    `}
`;

const RecoversContainer = styled.div`
  position: absolute;
  bottom: -157px;
  text-align: center;
  width: 94px;
  left: -33px;

  span {
    font-weight: normal;
    font-size: 12px;
    text-align: center;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #f7f5fb;
  }
`;
const RecoverTime = styled.div`
  font-style: normal;
  font-weight: 500;
  font-size: 25px;
  text-align: right;
  letter-spacing: -0.003em;
  color: #ffbd59;
`;

export const Stamina = ({ staminaLeft }) => {
  var d = new Date();
  var v = new Date();

  return (
    <Container className="position-absolute">
      <StaminaContainer>
        <StaminaFlow progress={staminaLeft} />
      </StaminaContainer>
      <StaminaCount isActive={staminaLeft === 0} style={{ top: 435, left: -20 }}>
        0
      </StaminaCount>
      <StaminaCount isActive={staminaLeft === 20} style={{ top: 353 }}>
        20
      </StaminaCount>
      <StaminaCount isActive={staminaLeft === 40} style={{ top: 262 }}>
        40
      </StaminaCount>
      <StaminaCount isActive={staminaLeft === 60} style={{ top: 171 }}>
        60
      </StaminaCount>
      <StaminaCount isActive={staminaLeft === 80} style={{ top: 80 }}>
        80
      </StaminaCount>
      <StaminaCount isActive={staminaLeft === 100} noTick style={{ top: 0 }}>
        100
      </StaminaCount>
      <EggStaminaContainer src={EggStamina} />
      <RecoversContainer>
        <span>Stamina recovers in</span>
        <Countdown
          date={v.setMinutes(d.getMinutes() + 30)}
          renderer={({ hours, minutes, seconds }) => (
            <RecoverTime>
              {hours}:{minutes}:{seconds}
            </RecoverTime>
          )}
        />
      </RecoversContainer>
    </Container>
  );
};
