const programID = new anchor.web3.PublicKey("Ca9PwZXaSgxtUg9kSPfYhAE9HgYyF36fb5W2ZQ6PMoHq");
export class EggIncubatorData {
  incubated = 0;
  walks = 0;
  kisses = 0;
  song = 0;
  hug = 0;
  story = 0;
  energy_bar = 0;
  last_energy_updated = 0;

  constructor(args) {
    if (args) {
      this.incubated = args.incubated;
      this.walks = args.walks;
      this.kisses = args.kisses;
      this.song = args.song;
      this.hug = args.hug;
      this.story = args.story;
      this.energy_bar = args.energy_bar;
      this.last_energy_updated = args.last_energy_updated;
    }
  }
}

class InitGameArgs {
  instruction = 0;
  i;
  constructor(args) {
    this.i = args.i;
  }
}

class InteractArgs {
  instruction = 2;
  interaction_type;
  constructor(args) {
    this.interaction_type = args.interaction_type;
  }
}

export const EGG_SCHEMA = new Map([
  [
    InitGameArgs,
    {
      kind: "struct",
      fields: [
        ["instruction", "u8"],
        ["i", "u8"],
      ],
    },
  ],
  [
    InteractArgs,
    {
      kind: "struct",
      fields: [
        ["instruction", "u8"],
        ["interaction_type", "u8"],
      ],
    },
  ],
  [
    EggIncubatorData,
    {
      kind: "struct",
      fields: [
        ["incubated", "u32"],
        ["walks", "u16"],
        ["kisses", "u16"],
        ["song", "u16"],
        ["hug", "u16"],
        ["story", "u16"],
        ["energy_bar", "u8"],
        ["last_energy_updated", "u32"],
      ],
    },
  ],
]);

export const EGG_ACCOUNT_SIZE = serialize(EGG_SCHEMA, new EggIncubatorData()).length;

